.calculator {
    width: 300px;
  margin: 100px auto;
  padding: 20px;
  border-radius: 10px;
  background-color: #f7f7f7;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
}
  .display {
    text-align: right;
    margin-bottom: 20px;
    padding: 10px;
    border: 2px solid #ddd;
    font-size: 24px;
  }
  
  .keypad {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 10px;
  }
  .result {
    font-size: 18px;
    color: green;
  }
  
  button {
    padding: 20px;
    font-size: 18px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    background-color: #eee;
    transition: background-color 0.3s;
  }
  
  button:hover {
    background-color: #ccc;
  }